import React from 'react';

class TestButton extends React.Component {
        constructor(props){
                super(props)
                this.handleClick_alert = this.handleClick_alert.bind(this)
        }

        handleClick_alert(){
                alert(window.getSelection().toString())
        }

        render(){
                return(
                        <button onClick={this.handleClick_alert}>test button</button>
                )
        }
}

export default TestButton;